.how {
  margin-bottom: 240px;

  @include lg {
    margin-bottom: 160px;
  }

  @include xs {
    margin-bottom: 115px;
  }
}

.how__title {
  margin-bottom: 42px;

  @include xs {
    margin-bottom: 31px;
  }
}

.how__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;

  @include lg {
    grid-template-columns: repeat(2, 1fr);
  }

  @include xs {
    display: block;
  }
}

.how__item {
  display: flex;
  flex-direction: column;

  @include xs {
    width: 100%;
    margin: 0 auto;

    &:not(:last-child) {
      margin-bottom: 35px;
    }
  }

  &:nth-child(1) {
    position: relative;
    margin-bottom: 126px;

    .how__decr {
      border: 2px solid $color_red_deep;
      border-bottom: none;
    }

    // .how__icon {
    //   background-image: url("../images/icon_serv_letter.svg");
    // }

    @include lg {
      margin-bottom: 80px;
    }

    @include xs {
      margin-bottom: 35px;
    }
  }

  // &:nth-child(2) {
  //   .how__icon {
  //     background-image: url("../images/icon_serv_eye.svg");
  //   }
  // }

  // &:nth-child(3) {
  //   .how__icon {
  //     background-image: url("../images/icon_serv_check.svg");
  //   }
  // }

  // &:nth-child(4) {
  //   .how__icon {
  //     background-image: url("../images/icon_serv_dollar.svg");
  //   }
  // }

  &:nth-child(n + 2) {
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 126px;
    }

    @include lg {
      &::after {
        height: 80px;
      }
    }

    @include xs {
      &::after {
        display: none;
      }
    }
  }
}

.how__decr {
  flex: 1 1 auto;
  padding: 62px 32px 40px 24px;
  border: 1px solid $color_grey_f2;
  transition: border-color 0.2s ease-out;
}

.how__icon {
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 34px;
  // background: $color_red_deep center / 40px 40px no-repeat;
  background-color: $color_red_deep;
  transition: background-color 0.2s ease-out;

  img {
    display: block;
    width: 40px;
    height: 40px;
  }
}

.how__name {
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 24px;
  color: $color_red_deep;
  transition: color 0.2s ease-out;
}

.how__text {
  font-weight: 300;
  font-size: 18px;
  line-height: 140%;
}

.how__button {
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  height: 126px;
  justify-content: flex-start;
  padding-right: 20px;
  color: $color_white;
  background-color: $color_red_deep;

  @include xl {
    gap: 0 20px;
    padding: 44px 20px 44px 30px;
  }

  @include lg {
    height: 80px;
    padding: 0 50px 0 40px;
  }

  @include xs {
    position: static;
    height: 80px;
    padding: 0 20px;
  }
}

